@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Poppins:wght@200;400;500;600;700&display=swap);
@charset "UTF-8";

.main .socials ul {
    display: flex;
    list-style: none;
    align-items: center;
    margin: auto;
    padding: 0;
    font-size: 1rem;
}

.main .socials i {
    color: black;
}

.main .socials li {
    padding-right: 25px;
}

.main .socials i:hover {
    color: #ffb3c8;
    transition: .5s;
    cursor: pointer;
}

.hello {
    padding-right: 0.25em;
}

.name {
    display: flex;
    font-size: 3rem;
    font-family: 'Poppins', sans-serif;  
    font-weight: 700;
}

.letter, .emoji {
    color: #ffb3c8;
    transition: transform 1s;
    cursor: pointer;
}

.letter {
    text-shadow: 2px 2px #bd4c6e;
}

.emoji {
    padding-left: 0.25em;
}

.letter:hover, .emoji:hover {
    transform: translateY(-1rem);
    cursor: pointer;
}

.info p {
    text-align: justify;
}

a, a:visited {
    text-decoration: none;
    color: black;
    font-weight: 600;
}

a:hover {
    color: #ffb3c8;
    transition: ease 0.2s;
}


/* responsive media query */

/* phone */
@media (max-width: 600px) {
    .name {
        font-size: 1.2rem;
    }
    .main p {
        font-size: 0.8rem;
    }
    .main .socials ul {
        font-size: 0.8rem;
        
    }
    .main .socials li {
        padding-right: 15px;
    }
}

/* portrait tablets + large phones */
@media (min-width: 600px) {
    .name {
        font-size: 2rem;
    }
}

/* landscape tablets */
@media (min-width: 768px) {
    .name {
        font-size: 2.5rem;
    }
    .main p {
        font-size: 0.5rem;
    }
}

/* laptops/desktops */
@media (min-width: 992px) {
    .main p {
        font-size: 1rem;
    }
}
.nav {
    float: right;
    padding-right: 2.5rem;
}

.d3l {
    font-size: 1rem;
}

.nav ul {
    list-style: none;
    margin: 0;
    padding-top: 2rem;
    width: 50%;
    height: 100%;
}

.nav li, .d3l {
    padding-top: 0.5rem;
}

.nav a {
    text-decoration: none;
    color: black;
    font-weight: 700;
    position: relative;
}

.nav img {
    --outline: 3px solid rgb(255, 250, 246);
    outline: var(--outline);
    border: var(--outline);
    height: auto;
    border-radius: 50%;
    transition: transform ease 0.25s;
  }

.nav img:hover,
.nav img:focus {
    outline: var(--outline);
    transform: scale(1.1);
}

.nav img.active {
    --pink: #ffb3c8;
    --outline: 3px solid var(--pink);
    color: var(--pink);
    outline: var(--outline);
    border: var(--outline);
}

.nav li a {
    color: black;
    position: relative;
    text-decoration: none;
}

.nav li a::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 0.8rem;
    background-color: #ffb3c8;
    bottom: 0;
    left: 0;
    z-index: -1;
    transform-origin: right;
    transform: scaleX(0);
    transition: transform .3s ease-in-out;
}

.nav li a:hover::before {
    transform-origin: left;
    transform: scaleX(1);
}

.nav li a.active::before {
    width: 100%;
    height: 0.8rem;
    background-color: #ffb3c8;
    bottom: 0;
    left: 0;
    z-index: -1;
    transform: scaleX(1);
}

.d3l a:hover {
    color: #ffb3c8;
}

/* phone */
@media (min-width: 0px) {
    nav {
        padding-left: 5rem;
    }
    .nav li {
        font-size: 1.1rem;
    }
    .nav img {
        width: 6rem;
    }

    .d3l {
        width: 100%;
        padding-left: 1.5rem;
    }
}

@media (min-width: 600px) {
    .nav li {
        font-size: 1.5rem;
    }

    .nav img {
        width: 7rem;
    }

    .d3l {
        width: 100%;
        padding-left: 2rem;
    }
}


/* PROJECTS */
@keyframes float {
	0% {
		transform: translatey(0%);
	}
	50% {
		transform: translatey(-1rem);
	}
	100% {
		transform: translatey(0%);
	}
}

.projects {
    transform: translatey(0px);
    animation: float 6s ease-in-out infinite;
}

.gallery {
    width: 75%;
    padding-right: 2rem;
    box-sizing: border-box;
}

.row {
    display: flex;
    flex-wrap: wrap;
    padding: 5px;
}

.box {
    margin: 0 1.5rem 1.5rem 0;
    flex: 25% 1;
    max-width: 11rem;
    position: relative;
    float: left;
}
  
.box img {
    vertical-align: middle;
    width: 100%;
    border-radius: 20%;
    transition: .1s transform;
    transform: translateZ(0); 
  }
  
.box:hover {
    z-index: 1;
  }
  
.box:hover img {
    transform: scale(1.5,1.5);
    transition: .3s transform;
    opacity: 0.3;
    height: auto;
  }
  
.box .overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .3s ease;
  }

.box:hover .overlay {
    opacity: 1;
  }
  
.project-title {
    font-size: 1.3rem;
    font-weight: 600;
}

.tools {
    font-size: 1rem;
}

.description {
    position: relative;
    display: flex;
    width: 100%;
    margin-bottom: 1em;
    color: black;
    overflow-wrap: anywhere;
    background: rgb(255, 255, 255, 0.5)
}

.gallery .fab {
    color:black;
    display: inline-flex;
    padding-right: 1rem;
    font-size: 2rem;
}

.gallery .fab:hover{
    color: #ffb3c8;
    transition: ease 0.2s;
}
  
.cf:before, .cf:after {
    display: table;
    content: "";
    line-height: 0;
  }
  
  .cf:after {
    clear: both;
  }

@keyframes fillLeft {
    100% {
        right: 100%;
    }
}

@keyframes fillTop {
    100% {
        top: 100%;
    }
}

@keyframes fillLeftOdd {
    100% {
        left: 100%;
    }
}

/* responsive media query */

@media (min-width: 0px) {
    .gallery {
        width: 100%;
    }
    .box {
        flex: 50% 1;
    }
    .overlay .project-title{
        font-size: 1rem;
    }
    .overlay .tools {
        font-size: 1rem;
    }
    .overlay .description {
        font-size: 1rem;
    }
}

@media (min-width: 992px) {
    .gallery {
        width: 95%;
    }
}

/* laptops/desktops */
@media (min-width: 1283px) {
    .gallery {
        width: 75%;
    }
}
.experience {
    padding-bottom: 15rem;
}

@media only screen and (min-width: 0px) {

    /* box */
    .experience .exp-box {
        box-shadow: rgba(0, 0, 0, 0.01) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
        padding-bottom: 2rem;
        background-color: white;
        margin: 1rem 0 1rem 0;
        width: 75%;
        border-radius: 25px;
    }
    .experience .exp-box:hover {
        transition: ease .3s;
        background-color:  #ffe6ee;
        cursor: pointer;
    }

    .experience .header:hover {
        color: #bd4c6e;
        transition: ease .3s;
    }

    .experience .exp-box .location {
        font-weight: 400;
    }
    /* endregion */

    /* box header */
    .experience .exp-box .header {
        padding: 1rem 1rem 10rem 1rem;
    }
    .experience img {
        width: 4rem;
        border-radius: 75%;
        float: center;
    }
    .experience h2 {
        font-size: .9rem;
        vertical-align: center;
    }
    .experience h2.left {
        font-weight: 600;
        width: 100%;
        text-align: center;
    }
    .experience h2.right {
        font-weight: 400;
        margin-top: 0;
        width: 100%;
        text-align: center;
    }
    /* endregion */

    /* box content */
    .experience ul li {
        margin: 1rem 1rem 0 0;
        font-size: .8rem;
        list-style-type: none;
    }
    /* endregion */
}

@media only screen and (min-width: 600px) {
    .experience h2 {
        font-size: 1rem;
    }
    .experience ul li {
        font-size: 0.9rem;
    }
    
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
    .experience .exp-box {
        width: 80%;
    }

    .experience .exp-box .header {
        padding-bottom: 2rem;
    }
    .experience .pic {
        width: 10%;
        float: left;
    }
    .experience img {
        width: 3.5rem;
    }
    .experience .title, .location {
        margin-left: 4.5rem;
    }
    .experience h2.left {
        width: 65%;
        float: left;
        text-align: left;
        font-size: 1rem;
        margin: 0;
    }
    .experience h2.right {
        width: 35%;
        font-size: 1rem;
    }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
    .experience .exp-box {
        width: 65%;
    }
    .experience h2.left {
        width: 65%;
        font-size: 1rem;
    }
    .experience .exp-box .header {
        padding-bottom: 2.3rem;
    }
}

@media only screen and (min-width: 1500px) {
    .experience .exp-box {
        width: 60%;
    }
}

@media only screen and (min-width: 1750px) {
    .experience .exp-box {
        width: 50%;
    }
}
/* scrollbar */
html {
    scroll-behavior: smooth;
}

 ::-webkit-scrollbar {
    width: 10px;
}

 ::-webkit-scrollbar-track {
    background-color: transparent;
}

 ::-webkit-scrollbar-thumb {
    background-color: #ffb3c8;
    border-radius: 10px;
}
/* endregion */

.melody {
    position: fixed; 
    bottom: 0px; 
    right: 0px;
}

body {
    margin: 0;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 1.2rem;
    background: rgb(255, 250, 246);
    overflow: scroll
}
  
.content {
    padding-top: 15%;
}

.left {
    float: left;
    width: 42%;
}

.right {
    float: right;
    width: 58%;
}

h1 {
    width: 80%;
}

p {
    width: 60%;
    text-align: justify;
    word-spacing:-2px;
}

@media only screen and (min-width: 0px) {
    h1 {
        font-size: 1.5rem;
    }
    h3 {
        font-size: 1rem;
    }
    p {
        width: 85%;
        font-size: 1rem;
        text-align: left;
        word-spacing: 0px;
    }
    .content {
        padding-top: 15%;
    }
    .melody {
        width: 7rem;
    }
}

@media (min-width: 768px) {
    h1 {
        font-size: 2rem;
    }
    h2 {
        font-size: 1.5rem;
    }
    .melody {
        width: 10rem;
        padding: 0;
    }
}

@media (min-width: 1200px) {
    body {
        font-size: 1rem;
    }
    p {
        width: 50%;
    }
    .content {
        padding-top: 10%;
    }
    .melody {
        width: 15rem;
        padding: 0;
    }
}

